import React, { Component } from 'react';
import {
  StyleSheet,
  Text,
  View,
  FlatList,
  TouchableOpacity,
  Button,
  ScrollView,
  SectionList
} from 'react-native';

import {withRouter} from 'react-router'


import tempevents from "../tempdata/TempDataFile.js"
import EventElement from "../components/EventElement.js"
import DetailsComponent from "../components/DetailsComponent.js"
import FeaturedEvents from "../components/FeaturedEvents.js"

export default class HomeScreen extends Component {
  constructor(props)
  {
    super(props)
    this.state =
    {
      events: null,
      modalvisible: false,
      isFetching: false
    }
  }

  fetchData(){
    fetch("https://hangouter-backend.herokuapp.com/api/v1/events")
    .then(response => response.json())
    .then((responseJson)=> {
      this.setState({
       events: this.sortEvents(responseJson)
      })
      this.setState({
        sectiondata: this.processEvents(this.state.events)
      })

      console.log(this.state.events)
      console.log(this.state.sectiondata)
      this.setState({isFetching: false})
    })
    .catch(error=>console.log(error)) //to catch the errors if any


  }

  processEvents(events){
    if (!events)
      return []
    var sectiondata = [
      {
        title: "Past Events",
        data: []
      },
      {
        title: "Events Today",
        data: []
      },
      {
        title: "Events in Future",
        data: []
      }];

    events.forEach(function (item, index) {
      var start = new Date(item.starttime)
      var end = item.endtime ? new Date(item.endtime) : null
      var today = new Date()
      if (HomeScreen.isActive(item)){
          if (end){
            start.setHours(0);
            start.setMinutes(0);
            end.setHours(0);
            end.setMinutes(0);
            if (start <= today && today <= end)
              sectiondata[1].data.push(item)
            else
              sectiondata[2].data.push(item)
          }else{
            start.setHours(0);
            start.setMinutes(0);
            if (start==today)
              sectiondata[1].data.push(item)
            else
            sectiondata[2].data.push(item)
          }
      }else{
        sectiondata[0].data.push(item)
      }
    });
    return sectiondata
  }
  sortEvents(events){
    return events.sort((a,b) => {
      var adate = new Date(a.starttime)
      var bdate = new Date(b.starttime)
      if (adate > bdate){
        return -1
      }else if (adate==bdate){
        return 0
      }else{
        return 1
      }
    })
    return events
  }

  componentDidMount(){
    this.fetchData()
  }

  onRefresh() {
   this.setState({ isFetching: true }, function() { this.fetchData() });
  }

  static isActive(event){
    var active = true
    if (event.endtime){
      if (new Date() > new Date(event.endtime))
        active = false
    }else{
      var enddate = new Date(event.starttime)
      enddate.setHours(0);
      enddate.setMinutes(0);
      enddate.setDate(enddate.getDate() + 1)
      if (new Date() > enddate)
        active = false
    }
    return true
    //return active
  }

  _flatListRenderer({item}){
    var Element = withRouter(({history}) => (
      <TouchableOpacity onPress={() => {history.push("/details/" + item._id, {item: item})}}>
          <EventElement active={HomeScreen.isActive(item)} item={item}/>
      </TouchableOpacity>))
    return ( <Element/> )
  }

  _sectionListRenderer({item}){
    return (
    <FlatList style={styles.flatliststyle}
    data={this.state.events}
    renderItem={this._flatListRenderer.bind(this)}
    keyExtractor={(item,index) => item._id}
    onRefresh={() => this.onRefresh()}
    refreshing={this.state.isFetching}
    />
  )
  }

  render() {
    return (
      <View style={styles.container}>
        <ScrollView>
        <View style={styles.featured}>
          <FeaturedEvents/>
        </View>
        <FlatList style={styles.flatliststyle}
        data={this.state.events}
        renderItem={this._flatListRenderer.bind(this)}
        keyExtractor={(item,index) => item._id}
        onRefresh={() => this.onRefresh()}
        refreshing={this.state.isFetching}
        />

        </ScrollView>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  container:{
    flex: 1,
    justifyContent: 'center',
    alignItems: 'stretch',
    backgroundColor: '#DDD'
  },
  featured: {
    height: 200,
  },
  title:{
    color: '#000',
    fontSize: 30,
  },
  flatliststyle:{
    flex: 5,
  }
});
